import { useState, useEffect } from 'react';
import * as MovieAPI from '../fetch.js';

export default function MovieSubReviews({ movieId }) {
  const [movieReview, setMovieReview] = useState([]);

  useEffect(() => {
    MovieAPI.fetchMovieByIdReviews(movieId).then(movie =>
      setMovieReview(movie.results),
    );
  }, [movieId]);
  return (
    <>
      {movieReview && (
        <>
          <ul>
            {movieReview.map(movie => (
              <li key={movie.id}>
                <h4>Author: {movie.author}</h4>
                <p>{movie.content}</p>
              </li>
            ))}
          </ul>
        </>
      )}
      {movieReview.length === 0 && (
        <p>We don't have any reviews for this movie</p>
      )}
    </>
  );
}
